@use '../util';

html {
    box-sizing: border-box;
    font-size:  100%;
}

*, 
*::before, 
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    
    
    background-color: var(--background-color);
    color: var(--text-color);
    
}

a{
    text-decoration: none;
}